<!--注销登录-->
<template>
	<mdialog :configs="configs">ddd</mdialog>
</template>
<script>
	export default{
		data(){
			return{
				configs:{
					visiable:true,
					text:'确定要注销吗？',
					cancel:'true',
					callback:this.logout,
				}
			} 
		},
		methods:{
			logout(){
				localStorage.removeItem('user');
				self.location=document.referrer
				// this.$route.router.go({ name: 'index' });
			}
		},
		components:{
			mdialog:require('../components/dialog.vue')
		}
	}
</script>





